<template>
  <div class="article">
    <ul>
      <router-link tag="li" to="/">
        <a>На основную</a>
      </router-link>
    </ul>
    <p class="article__title">{{ title }}</p>
    <h1>Декоративные черточки у заголовка</h1>
    <p>Мы получаем от дизайнера пример вот таких заголовков с
      горизонтальными линиями слева и справа от текста.
      Реализация в css очень проста, начнем!</p>
    <p>Для начала нам нужен сам заголовок, так как их несколько на
      одной страницы пуская это будет заголовки второго
      уровня &lt;h2&gt;. На этом разметка закончена.
    </p>

    <pre><code>
      &lt;h2&gt;Typographi&lt;/h2&gt;
      &lt;h2&gt;Typographi in web&lt;/h2&gt;
      &lt;h2&gt;Best practices for using typographi in web desing&lt;/h2&gt;
    </code></pre>
    <p>Теперь стили, если добавить к нашему
      заголовку псевдоэлементы ::before и ::after, не задавая позиционирование,а для самого заголовка задать display: flex; ,то наши псевдоэломенты автоматически становятся флекс-элементами
      и начинают вести себя соответствующе.
      По дизайну черточки у нас 20px шириной и 2px высотой, такого же цвета как
      и текст, и расположены на расстоянии минимум
      10px от текста. Сам заголовок при достижении 400px ширины
      переносится на новую строку. При всех этих
      условиях у нас получаются вот такие стили.</p>
    <pre><code>
      h2 {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 460px;
      margin: 0 auto 40px;
      text-align: center;
      text-transform: uppercase;
      color: #fff;
      }

      h2::before,
      h2::after {
      content: '';
      flex: 0 0 20px;
      height: 2px;
      background-color: #fff;
      }

      h2::before {
      margin-right: 10px;
      }

      h2::after {
      margin-left: 10px;
      }
    </code></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: this.$router.currentRoute.params.id,
    };
  },
  watch: {
    $route(toR, fromR) {
      this.title = toR.params.id;
    },
  },
};
</script>

<style scoped lang="scss">
  @import "../scss/components/article";
</style>
